import React from 'react';
import styled from 'styled-components';
import { useState, useEffect, useCallback } from 'react';
import { ref, uploadBytesResumable, getDownloadURL } from 'firebase/storage';
import storage from './FireBase';
import newDocument from '../../../images/new-document.png';
import upload from '../../../images/upload.png';
// const analytics = getAnalytics(app);

const CV = () => {
    const [files, setFileList] = useState([]); //  
    const [isUploading, setUploading] = useState(false); //  
    const [photoURL, setPhotosURL] = useState([]); //    
    const [progress, setProgress] = useState(0); //  

    const handleImageChange = (e) => {
        for (const image of e.target.files) {
            setFileList((prevState) => [...prevState, image]);
        }
    };

    //   
    const handleImageUpload = async (e, fileList) => {
        console.log('try');
        try {
            setUploading(true);
            console.log('try');
            //    Promise.all    url 
            const urls = await Promise.all(
                fileList?.map((file) => {
                    //       .    images   
                    const storageRef = ref(storage, `images/${file.name}`);

                    // File  Blob   uploadBytes  uploadBytesResumable  
                    //  base64  data_url     uploadString 
                    //   https://firebase.google.com/docs/storage/web/upload-files  
                    const task = uploadBytesResumable(storageRef, file);

                    //   ,     
                    task.on('state_changed', (snapshot) => {
                        setProgress(Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100));
                    });
                    return getDownloadURL(storageRef);
                })
            );
            //      (   () )
            setPhotosURL(urls);
            alert('  ');
        } catch (err) {
            console.error(err);
        }
        // 
        setProgress(0);
        setUploading(false);
    };

    return (
        <BackContainer>
            <Container>
                <h1></h1>
                <button onClick={(e) => handleImageUpload(e, files)}>asdasd</button>
                {progress}%
                <List>
                    <Item>
                        <AddItem />
                        <h5>  </h5>
                    </Item>
                    <Item>
                        <UploadItem multiple accept="/*" type="file" onChange={handleImageChange} />
                        <h5> </h5>
                    </Item>

                    <Item>
                        <ItemTitle>
                            <h3></h3> <p></p>
                        </ItemTitle>
                        <ItemAction>asd</ItemAction>
                    </Item>
                    {photoURL?.length >= 0 &&
                        photoURL.map((url, index) => (
                            <Item>
                                <img src={url} alt="  " />
                            </Item>
                        ))}
                </List>
                <div>{progress}</div>
            </Container>
        </BackContainer>
    );
};

const BackContainer = styled.div`
    background-color: #f8f8fa;
    width: 100vw;
    height: 100vh;
`;

const Container = styled.div`
    margin: auto;
    width: 1060px;
    padding-top: 50px;

    h1 {
        margin-top: 50px;
        font-size: 24px;
    }
`;
const List = styled.div`
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;
`;
const Item = styled.div`
    position: relative;
    width: 22%;
    height: 200px;
    background-color: #ffffff;
    border: 1px solid #d0d0d0;
    margin-right: 20px;
    margin-bottom: 20px;

    flex-direction: column;
    &:nth-child(1),
    &:nth-child(2) {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    img {
        width: 100%;
    }
    h5 {
        font-size: 20px;
        font-weight: 600;
        letter-spacing: normal;
        text-align: center;
        color: #333;
        margin: 20px 0 0;
    }
`;

const AddItem = styled.input`
    cursor: pointer;
    width: 74px;
    height: 74px;
    margin: 0 auto;
    border-radius: 50%;
    border: none;
    background-color: #5757ff;
    background-image: url(${newDocument});
    background-repeat: no-repeat;
    background-position: 20px center;
    background-size: 50%;
`;

const UploadItem = styled(AddItem)`
    background-image: url(${upload});
    background-color: #8c8c8c;
    color: #8c8c8c;
    &::file-selector-button {
        display: none;
    }
`;

const ItemTitle = styled.div`
    padding: 6px 20px 20px;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: normal;
    text-align: left;
    h3 {
        font-size: 18px;
        font-weight: 600;
        line-height: 1.33;
        max-height: 46px;
        max-width: 100%;
        letter-spacing: normal;
        text-align: left;
        color: #333;
        overflow: hidden;
        word-break: break-all;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        border: none;
    }
    p {
        color: #999;
        margin-top: 5px;
    }
`;

const ItemAction = styled.div`
    position: absolute;
    bottom: 0;
    height: 41px;
    width: calc(100% - 20px);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    padding: 0 12px 0 20px;
    -ms-flex-align: center;
    align-items: center;
    border-top: 1px solid #e0e0e0;
`;

export default CV;
